import Vue from 'vue'
import App from './App.vue'

Vue.config.productionTip = false;

/* eslint-disable no-new */
global.vmroot = new Vue({
  el: '#app',
  data: {
    section: {}
  },
  template: '<App :section="section"/>',
  components: {App}
});

global.vmroot.$watch('section', function (newValue, oldValue) {
  // 这个回调将在 `vm.a` 改变后调用
  console.log('======')
  console.log(newValue)
})



